<template>
  <div class="detail">
    <div class="detailContent">
      <div class="detailTitle">
        <span class="back" @click="goBack">文章管理</span>
        <i class="el-icon-arrow-right"></i> 
        <span>文章详情</span>
      </div>
      <div class="article">
        <div class="articleContent">
          <h1 class="blogTitle">{{blog.title}}</h1>
          <div class="blogName">
            <p>
              <span>{{blog.userNickname}}</span>
              <em>{{formatDate(blog.createTime)}}</em>
            </p>
            <div class="blogContent">
              <div class="blogText" v-html="blog.content" ref="blogText">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .detailContent {
    margin: 60px 0 60px 260px;
    .detailTitle{
      color: #808080;
      height: 58px;
      font-size: 14px;
      line-height: 58px;
      text-align: left;
      padding-left: 50px;
      border-bottom: 1px solid #eee;
      .back{
        color: black;
        cursor: pointer;
      }

    }
    .article{
      padding: 20px 50px;
      .articleContent{
        border: 1px solid #ccc;
        padding: 20px 200px;
        overflow: hidden;
        .blogTitle{
          font-size: 20px;
        }
        .blogName{
          text-align: left;
          .blogText{
            margin: 40px 0;
            font-size: 16px;
          }
          em{
            font-style: normal;
            color: #ccc;
            margin-left: 20px;
          }
        }
      }
    }
  }
</style>
<script>
export default {
  name:'articleDetail',
  data () {
    return {
      blog : {}
    }
  },
  created() {
    this.getDetails()
  },
  mounted() {

  },
  methods:{
    goBack() {
       this.$router.back(-1)
    },
    getDetails() {
      let url = this.api.configIp + this.net.newsDetail;
      let id = this.$route.query.id;
      let params = {
        id: id
      };
      this.$post(url,params).then((res)=>{
        console.log('sucess:',res)
				if(res.code==200 && res.obj!=null){
          this.blog = res.obj
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
  }
}
</script>


